import React, { useState, useEffect } from 'react'
import {
  Box,
  Card,
  CardContent,
  Typography,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  Chip,
  CircularProgress,
  Alert,
  Button,
  Tooltip
} from '@mui/material'
import {
  Refresh as RefreshIcon,
  CheckCircle as CheckCircleIcon,
  Error as ErrorIcon,
  Warning as WarningIcon,
  Info as InfoIcon
} from '@mui/icons-material'
import { apiClient } from '@/api/client'

interface ProxyStatusData {
  proxyid: number
  message: string
  proxyServiceAlive: string
  proxyServiceStart: string
  controller_type: string
  proxy_ip: string
  proxy_port: string
  remark: string
}

const ProxyStatus: React.FC = () => {
  const [proxyStatusList, setProxyStatusList] = useState<ProxyStatusData[]>([])
  const [loading, setLoading] = useState<boolean>(true)
  const [error, setError] = useState<string | null>(null)
  const [lastUpdated, setLastUpdated] = useState<Date | null>(null)

  const fetchProxyStatus = async () => {
    try {
      setLoading(true)
      setError(null)

      console.log('Fetching proxy status from /ProxyStatus')
      const response = await apiClient.get('/ProxyStatus')
      console.log('Proxy status response:', response)
      console.log('Proxy status data:', response.data)

      if (Array.isArray(response.data)) {
        console.log(`Received ${response.data.length} proxy status items`)
        response.data.forEach((item, index) => {
          console.log(`Item ${index}:`, item)
        })
        setProxyStatusList(response.data)
        setLastUpdated(new Date())
      } else {
        console.error('Response data is not an array:', response.data)
        setError('回應資料格式錯誤')
      }
    } catch (err: any) {
      console.error('獲取代理狀態失敗:', err)
      setError(err.response?.data?.detail || '無法獲取代理狀態資料')
    } finally {
      setLoading(false)
    }
  }

  useEffect(() => {
    fetchProxyStatus()
  }, [])

  const getStatusChip = (message: string, proxyServiceAlive: string, proxyServiceStart: string) => {
    const isAlive = proxyServiceAlive === '1'
    const isStarted = proxyServiceStart === '1'

    if (message === 'OK' && isAlive && isStarted) {
      return (
        <Chip
          icon={<CheckCircleIcon />}
          label="運行中"
          color="success"
          size="small"
        />
      )
    } else if (message.startsWith('HTTP') || message.startsWith('Error')) {
      return (
        <Chip
          icon={<ErrorIcon />}
          label="錯誤"
          color="error"
          size="small"
        />
      )
    } else if (message === 'NG_Timeout') {
      return (
        <Chip
          icon={<WarningIcon />}
          label="超時"
          color="warning"
          size="small"
        />
      )
    } else {
      return (
        <Chip
          icon={<InfoIcon />}
          label="未知"
          color="default"
          size="small"
        />
      )
    }
  }

  const formatMessage = (message: string) => {
    if (message.length > 20) {
      return (
        <Tooltip title={message}>
          <span>{message.substring(0, 20)}...</span>
        </Tooltip>
      )
    }
    return message
  }

  if (loading) {
    return (
      <Box
        display="flex"
        justifyContent="center"
        alignItems="center"
        minHeight="400px"
      >
        <CircularProgress />
        <Typography variant="h6" sx={{ ml: 2 }}>
          載入代理狀態中...
        </Typography>
      </Box>
    )
  }

  if (error) {
    return (
      <Alert
        severity="error"
        action={
          <Button
            color="inherit"
            size="small"
            onClick={fetchProxyStatus}
            startIcon={<RefreshIcon />}
          >
            重試
          </Button>
        }
      >
        {error}
      </Alert>
    )
  }

  return (
    <Box>
      <Box display="flex" justifyContent="space-between" alignItems="center" mb={3}>
        <Typography variant="h4" component="h1">
          代理服務狀態監控
        </Typography>
        <Button
          variant="contained"
          startIcon={<RefreshIcon />}
          onClick={fetchProxyStatus}
          disabled={loading}
        >
          重新整理
        </Button>
      </Box>

      {lastUpdated && (
        <Typography variant="body2" color="text.secondary" mb={2}>
          最後更新時間: {lastUpdated.toLocaleString('zh-TW')}
        </Typography>
      )}

      <Card>
        <CardContent>
          <TableContainer component={Paper} variant="outlined">
            <Table>
              <TableHead>
                <TableRow>
                  <TableCell>代理 ID</TableCell>
                  <TableCell>狀態</TableCell>
                  <TableCell>訊息</TableCell>
                  <TableCell>服務運行</TableCell>
                  <TableCell>服務啟動</TableCell>
                  <TableCell>控制器類型</TableCell>
                  <TableCell>IP 位址</TableCell>
                  <TableCell>連接埠</TableCell>
                  <TableCell>備註</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {proxyStatusList.length === 0 ? (
                  <TableRow>
                    <TableCell colSpan={9} align="center">
                      <Typography variant="body2" color="text.secondary">
                        沒有代理服務資料
                      </Typography>
                    </TableCell>
                  </TableRow>
                ) : (
                  proxyStatusList.map((status) => (
                    <TableRow key={status.proxyid} hover>
                      <TableCell>
                        <Typography variant="body2" fontWeight="medium">
                          {status.proxyid}
                        </Typography>
                      </TableCell>
                      <TableCell>
                        {getStatusChip(status.message, status.proxyServiceAlive, status.proxyServiceStart)}
                      </TableCell>
                      <TableCell>
                        {formatMessage(status.message)}
                      </TableCell>
                      <TableCell>
                        <Box
                          sx={{
                            width: 16,
                            height: 16,
                            borderRadius: '50%',
                            backgroundColor: status.proxyServiceAlive === '1' ? '#4caf50' : '#f44336',
                            display: 'inline-block'
                          }}
                        />
                      </TableCell>
                      <TableCell>
                        <Box
                          sx={{
                            width: 16,
                            height: 16,
                            borderRadius: '50%',
                            backgroundColor: status.proxyServiceStart === '1' ? '#4caf50' : '#f44336',
                            display: 'inline-block'
                          }}
                        />
                      </TableCell>
                      <TableCell>
                        <Typography variant="body2">
                          {status.controller_type || '-'}
                        </Typography>
                      </TableCell>
                      <TableCell>
                        <Typography variant="body2">
                          {status.proxy_ip || '-'}
                        </Typography>
                      </TableCell>
                      <TableCell>
                        <Typography variant="body2">
                          {status.proxy_port || '-'}
                        </Typography>
                      </TableCell>
                      <TableCell>
                        <Typography variant="body2">
                          {status.remark || '-'}
                        </Typography>
                      </TableCell>
                    </TableRow>
                  ))
                )}
              </TableBody>
            </Table>
          </TableContainer>
        </CardContent>
      </Card>
    </Box>
  )
}

export default ProxyStatus